import * as React from "react"
import { Container } from "react-bootstrap"

//import { Link,useStaticQuery,graphql } from "gatsby"

const Footer = () =>{
        return(
            <footer bg="light" class="footer" expand="lg" className="mb-5">
                <Container >
                    <div class="text-muted d-flex align-items-center justify-content-center">© {new Date().getFullYear()} hirame's memopad</div>
                </Container>
            </footer>
        )
}

export default Footer